import businList from "./components/businList.vue"; // 附近商家列表
import GriadList from "./components/griadList.vue" //九宫格分类
import Banners from "./components/banners.vue" //广告轮播图 
import Title from "./components/title.vue" // 标题
import Preference from "./components/preference.vue" //为您优选
import Special from "./components/special.vue" //今日特价
import Json from '../../json';
// 引入接口
import {
	listing
} from '../../api/api.js'
// 引入请求地址
import {
	getAll,
	startingurl,
	imgurl,

} from '../../api/request.js'
export default {
	// 引入组件
	components: {
		businList, // 商家列表
		GriadList, //九宫格
		Banners, //广告轮播图 
		Title, //标题 
		Preference, //优选
		Special //今日特价
	},
	/*
	初始化数据
	*/
	data() {
		return {
			// 整个页面的loading
			efinition: true,

			loading: false,
			over: false,
			imgurl: "",
			CustomBar: this.CustomBar,
			isScroll: false,
			scrollTop: 0,
			isLocation: false,
			locationError: false,
			address: "昆明市",
			cuIconList: [],
			dotStyle: false,
			swiperList: [],
			cateList: [],
			storeList: [],
			discountList: [],
			modalName: null,
			toStore: null,
			sortCur: 0,
			synthesize: "排序",
			// selectType;//1-距离 2-评分 3-起送价  4-月销量
			// type;//0-高  1-低
			sortList: [{
					"name": "距离近到远",
					"selectType": 1,
					"type": 0
				},
				{
					"name": "距离远到近",
					"selectType": 1,
					"type": 1
				},
				{
					"name": "评分高到低",
					"selectType": 2,
					"type": 0
				},
				{
					"name": "评分低到高",
					"selectType": 2,
					"type": 1
				},
				{
					"name": "起送价低到高",
					"selectType": 3,
					"type": 0
				},
				{
					"name": "起送价高到低",
					"selectType": 3,
					"type": 1
				},
				{
					"name": "月销量高到低",
					"selectType": 4,
					"type": 0
				},
				{
					"name": "月销量低到高",
					"selectType": 4,
					"type": 1
				}
			],

			// 多选
			screendata: [{
				"title": "商家特色(可多选)",
				"datas": [{
						"id": 1,
						"sign": 'deliver',
						"name": '0元起送'
					},
					{
						"id": 1,
						"sign": 'physi',
						"name": '免配送费'
					}
				],
			}],
			// 人均价筛选
			person: [{
				"title": "价格",
				"datas": [{
						"name": "20元以下",
						"per": {
							"$lt": 20
						},
					},
					{
						"name": "20-40元",
						"per": {
							"$lte": 40,
							"$gte": 20
						},
					},
					{
						"name": "40元以上",
						"per": {
							"$gt": 40
						},
					},
				]
			}],
			scrnum: -1,
			// 接收筛选的对象
			multiobj: {},
			count: 0
		};
	},
	computed: {
		pageScrollHeight() {
			let CustomBar = this.CustomBar;

			return `height:calc(100vh - ${CustomBar + 50}px);`;
		},
		storeScrollHeight() {
			let CustomBar = this.CustomBar;

			return `height:calc(100vh - ${CustomBar + 90}px);`;
		},
		sortName() {
			let sortCur = this.sortCur;
			let sortList = this.sortList;
			return sortCur > 6 ? sortList[0] : sortList[sortCur];
		},

		// 计算属性的 getter
		addressModify: function() {
			// `this` 指向 vm 实例
			let address = this.address;
			return address.length < 12 ? address : address.substr(0, 10) + "...";
		},

	},
	onLoad: function() {








		uni.createSelectorQuery()
			.select("#nav")
			.boundingClientRect(res => {
				console.log("筛选距离:", res.top);
				this.scrollTop = res.top;
			}).exec();

		// loading取消
		this.efinition = false
		// 数据请求
		setTimeout(() => {
			this.getCurLocation();
			this.getCuIconList();
			this.getCatelist();
			this.getStoreList();
			this.getSwiperList();
			this.getDiscountList()
			this.preference()
		}, 3000)
	},
	onShow: function() {
		if (
			this.gloablMyLocation.address !== null &&
			this.address !== this.gloablMyLocation.address
		) {
			//console.log("用户选择了位置");
			this.address = this.gloablMyLocation.address;
		}
	},
	methods: {
		//获取当前位置
		getCurLocation: function() {
			// //console.log("定位方法++++++++++")
			this.locationModel.getLocation(data => {
				if (!data.locationError) {
					let gloablMyLocation = this.gloablMyLocation;
					let baseInfo = data.baseInfo;
					gloablMyLocation.city = baseInfo.city;
					gloablMyLocation.aois = baseInfo.aois;
					gloablMyLocation.address = baseInfo.address;
					gloablMyLocation.location = baseInfo.location;
					gloablMyLocation.formattedAddress = baseInfo.formattedAddress;
					gloablMyLocation.addressComponent = baseInfo.addressComponent;
					this.address = baseInfo.address;
					this.isLocation = true;
				}
				this.locationError = data.locationError;
			});
		},
		// 分类cuIconList 列表数据
		getCuIconList: function() {
			this.cuIconList = Json.cuiconlist
		},

		// 今日特价  列表数据
		getDiscountList: function() {
			this.discountList = Json.discountlist
		},

		// 分类cuIconList 列表数据
		getCatelist: function() {
			this.cateList = Json.catelist
		},
		// 列表数据
		getStoreList: function() {
			// this.storeList = Json.storelist
			// getShopAll
		},

		preference() {
			// console.log("PPPPPPPPPPPP",getAll)
			let data = {
				schoolCode: 10673,
				// userExtId:3,
				pageNum: 1,
				pageSize: 10,
				// type: 'myorder',
				// openid: this.openid
			}
			// 批量并发请求多个接口，
			//Promise.all =>可以并发请求多个接口。并且同时得到多个接口的数据
			Promise.all([listing(getAll, data)])
				.then((res) => {
					console.log("res", JSON.stringify(res))
					// 附近商家列表
					this.imgurl = imgurl
					if (res[0].data.length === 0) {
						this.over = false;
						uni.showToast({
							title: "没有更多数据了~",
							icon: "none"
						})
						return
					}
					this.storeList = [...this.storeList, ...res[0].data]
				})
				.catch((err) => {
					console.log(err)
				})
		},

		// 列表数据
		getSwiperList: function() {
			this.swiperList = Json.swiperList
		},

		pageScroll: function(e) {
			let temp = this.scrollTop - 50 - this.CustomBar - 2;
			// 重要！！！多减2像素可降低滑动卡顿
			if (e.detail.scrollTop >= temp) {
				this.isScroll = true;
			} else {
				this.isScroll = false;
			}
		},
		//跳转至更多商家店铺
		goToMoreStore(e) {
			// console.log("P++++", e.currentTarget.dataset);
			uni.navigateTo({
				url: "/pages/home/morestore?id=" + e.currentTarget.dataset.id
			});
		},
		//跳转至商家店铺
		goToStore(e) {
			console.log("id++++", e);
			uni.navigateTo({
				url: "/pages/store/index?id=" + e.currentTarget.dataset.id
			});
		},
		showModal(e) {
			this.toStore = "nav";
			this.modalName = e.currentTarget.dataset.target;
			uni.pageScrollTo({
				scrollTop: this.scrollTop + this.CustomBar + 2,
			})
		},
		hideModal(e) {
			this.toStore = null;
			this.modalName = null;
		},

		//用户选择销量最高
		salHselect(e) {
			// this.toStore = "nav";
			uni.pageScrollTo({
				scrollTop: this.scrollTop + this.CustomBar + 2,

			})
			this.sortCur = e.currentTarget.dataset.id;
			uni.showToast({
				title: "要传的参数" + this.sortCur
			})
		},

		//用户选择速度最快
		fastSelect(e) {
			// this.toStore = "nav";
			this.sortCur = e.currentTarget.dataset.id;
			uni.showToast({
				title: "要传的参数" + this.sortCur
			})
		},


		//综合排序方式切换
		sortTap(index, name, selectType, type) {
			this.sortCur = index
			this.synthesize = name
			// console.log("综合排序选择+", screen + nums)
			this.starTing(selectType, selectType)
			this.toStore = null;
			this.modalName = null;
		},

		// 综合排序的请求
		starTing(selectType, type) {
			let Data = {
				selectType,
				type
			}
			listing(startingurl, Data)
				.then((res) => {
					console.log("排序请求数据", res)
					// 存储到vuex数据仓库
					// this.$store.commit('screenmuta',res.data)
				})
				.catch((err) => {
					console.log(err)
				})
			// console.log("触发综合排序请求打出参数:+", JSON.stringify(Data))
		},
		// 商家特色（多选）
		screenchoIce(indexs, sign) {
			console.log(sign)
			if (this.screendata[0].datas[indexs].id == 1) {
				// 选中
				this.screendata[0].datas[indexs].id = 2
				console.log('选中')
				this.$set(this.multiobj, sign, sign)
				// this.$set给对象添加一个属性
				console.log(this.multiobj)
			} else {
				// 未选中
				this.screendata[0].datas[indexs].id = 1
				console.log('未选中')
				this.$delete(this.multiobj, sign)
				console.log(this.multiobj)
			}
		},

		// 人均价（单选）
		personchoIce(index, name) {
			if (this.scrnum == index) {
				// 未选中
				this.scrnum = -1
				this.$delete(this.multiobj, 'capita')
				console.log(this.multiobj)
			} else {
				// 选中
				this.scrnum = index
				this.$set(this.multiobj, 'capita', name)
				console.log(this.multiobj)
			}
		},


		//筛选条件清空
		screenClear(e) {
			//console.log("清空筛选条件");
			// 商家特色
			this.screendata.forEach((item) => {
				let arr = item.datas.map((items) => {
					items.id = 1
					return items
				})
				console.log(arr)
			})
			this.multiobj = {}
			// 人均价
			this.scrnum = -1
		},
		//筛选条件确定
		screenComplete() {
			this.modalName = null;
			console.log("打出筛选条件", JSON.stringify(this.multiobj))

			// publicing(multipleurl, this.multiobj)
			// 	.then((res) => {
			// 		console.log(res)
			// 		// 存储到vuex数据仓库
			// 		this.$store.commit('screenmuta', res.data)
			// 	})
			// 	.catch((err) => {
			// 		console.log(err)
			// 	})
		},

		// 加载下一页
		scrolltolower() {
			console.log("到底了", this.storeList);
			// this.getStoreList()
			if (!this.over) {

			} else {
				this.loading = false;
			}
		}
	}
};
